import Bus from '@/utils/bus.js';
import {
  mapGetters
} from 'vuex';


export default {
  name: 'sidebar',
  data() {
    return {
      activeName: "/index",
    }
  },
  mounted() {
    let path = this.$route.path;
    if (path) {
      this.activeName = path;
    }
  },
  created() {},
  computed: {
    ...mapGetters(["menuPremissionList"]),
  },
  components: {},
  methods: {
    handleOpen() {},
    handleClose() {},
    //选择菜单
    handleSelect(e) {
      this.$router.replace({
        path: e
      });
    },
  }
}



